import { Form, message, Modal, Switch } from "antd";
import { axiosAuth } from "../../../config/axios";
import { CButton, CInput } from "../../Profile";

export default function CreateMedicines({ visible, close, setTags, tags, medicines, setMedicines, onlyCreate = false, medicalResultID }) {
    const handleAdMedicines = (values) => {
        if (onlyCreate) {


            axiosAuth({
                url: "/medical/add-medicine",
                method: "post",
                data: {
                    ...values,
                    medicalResultID
                },
            })
                .then((res) => {
                    message.success("To thnh cng");
                    console.log(res.data);
                })
                .catch((error) => {
                    message.error("C li xy ra");
                    console.log(error);
                });
        } else {
            setTags([...tags, values.name]);
            setMedicines([...medicines, values]);
        }
        close();
    };

    return (
        <Modal
            destroyOnClose
            centered
            title="Thm thuc"
            open={visible}
            onCancel={close}
            footer={null}
        >
            <Form
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 16 }}
                autoComplete="off"
                onFinish={handleAdMedicines}
                style={{ marginRight: "50px" }}
                initialValues={{
                    dosage: {
                        morning: 1,
                        afternoon: 1,
                        night: 1,
                    }
                }}
            >
                <Form.Item label="Tn thuc" name="name" rules={[{
                    required: true,
                    message: "Vui lng nhp tn thuc"
                }]}>
                    <CInput placeHolder="Tn thuc" />
                </Form.Item>
                <Form.Item label="S lng" name="quantity" rules={[{
                    required: true,
                    message: "Vui lng nhp s lng"
                }, {
                    min: 0,
                    message: "Vui lng nhp s lng"
                }
                ]}>
                    <CInput type="number" placeHolder="S lng" />
                </Form.Item>

                <Form.Item
                    name={["dosage", "ifAfter"]}
                    label="Sau khi n"
                    valuePropName="checked"
                    initialValue={true}
                >
                    <Switch defaultChecked />
                </Form.Item>
                <Form.Item
                    label="Liu ung sng"
                    name={["dosage", "morning"]}
                >
                    <CInput type="number" placeHolder="S liu bui sng" min={0} />
                </Form.Item>
                <Form.Item
                    label="Liu ung tra"
                    name={["dosage", "afternoon"]}
                >
                    <CInput type="number" placeHolder="S liu bui tra" min={0} />
                </Form.Item>
                <Form.Item label="Liu ung ti" name={["dosage", "night"]}>
                    <CInput type="number" placeHolder="S liu bui ti" min={0} />
                </Form.Item>

                <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                    <CButton type="primary" htmlType="submit">
                        To thuc
                    </CButton>
                </Form.Item>
            </Form>
        </Modal>
    )
}
