import { Form, message } from "antd";
import moment from "moment";
import React from "react";
// import { useHistory } from "react-router-dom";
import { axiosAuth } from "../../../config/axios";
import { CInput, DInput, FCenter, CButton } from "../../Profile";
import {useSelector} from "react-redux";

export const CreateRecord = ({ nextStep, setMedicalRecord, initialValues }) => {
    const [form] = Form.useForm();
    const [isLoading, setIsLoading] = React.useState(false);
    // const history = useHistory();
    const { username } = useSelector(state => state.user.user)

    const handleSubmit = (values) => {
        setIsLoading(true)
        axiosAuth({
            url: "/medical/add-medical-record",
            method: "post",
            data: {
                ...values,
                auth: values?.auth ? values?.auth : 'Bn thn',
                users: username
            },
        })
            .then((res) => {
                message.success("To thnh cng");
                nextStep();
                setMedicalRecord(res.data.data);
            })
            .catch((error) => {
                message.error("C li xy ra");
                console.log(error);
            });
        setIsLoading(false)
    };


    const handleUpdate = (values) => {
        setIsLoading(true)
        const data = {
            ...initialValues, ...values, birthday: moment(initialValues?.birthday).format("DD/MM/YYYY"),
        };

        axiosAuth({
            url: "/medical/update-medical-record",
            method: "post",
            data,
        })
            .then((res) => {
                // window.location.reload();
                message.success("Cp nht thnh cng")
                nextStep();
                setMedicalRecord(res.data.data);
            })
            .catch((error) => console.log(error));
        setIsLoading(false)
    };

    function disabledDate(current) {
        // Can not select days before today and today
        return current && current.valueOf() > moment(initialValues.createAt);
    }

    console.log(initialValues)

    return (
        <FCenter>
            {
                initialValues ?
                    <Form
                        labelCol={{ span: 8 }}
                        wrapperCol={{ span: 16 }}
                        // onFinishFailed={onFinishFailed}
                        autoComplete="off"
                        onFinish={handleUpdate}
                        style={{ marginRight: "50px" }}
                        initialValues={{
                            ...initialValues,
                            birthday: moment(initialValues?.birthday),
                            updateAt: moment(initialValues?.updateAt),
                            createAt: moment(initialValues?.createAt),
                        }}
                        disabled
                    // onSubmitCapture={handleUpdate}
                    >
                        <Form.Item label="Tn bnh nhn" name="fullName" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <CInput name="fullName" placeholder="Tn bnh nhn" />
                        </Form.Item>
                        <Form.Item label="Tn bnh" name="medicalName" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <CInput name="medicalName" placeholder="Tn bnh" />
                        </Form.Item>
                        <Form.Item label="Ngy sinh" name="birthday" >
                            <DInput name="birthday" placeholder="Ngy sinh" />
                        </Form.Item>
                        <Form.Item label="S in thoi" name="phone" rules={[{ required: true, message: 'Vui lng nhp thng tin' }, {
                            message: "Vui lng nhp ng S in thoi",
                            pattern: /(84|0[3|5|7|8|9])+([0-9]{8})\b/g
                        }]}>
                            <CInput name="phone" placeholder="S in thoi" />
                        </Form.Item>
                        <Form.Item label="a ch" name="address" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <CInput name="address" placeholder="a ch" />
                        </Form.Item>
                    </Form>
                    :
                    <Form
                        form={form}
                        name="basic"
                        labelCol={{ span: 8 }}
                        wrapperCol={{ span: 16 }}
                        autoComplete="off"
                        style={{ marginRight: "50px" }}
                        onFinish={handleSubmit}
                        initialValues={{
                            startDate: moment(),
                        }}
                    >
                        <Form.Item label="Tn bnh nhn" name="fullName" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <CInput name="fullName" placeholder="Tn bnh nhn" />
                        </Form.Item>
                        <Form.Item label="Tn bnh" name="medicalName" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <CInput name="medicalName" placeholder="Tn bnh" />
                        </Form.Item>
                        <Form.Item label="Ngy sinh" name="birthday" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <DInput name="birthday" placeholder="Ngy sinh" format="DD/MM/YYYY" />
                        </Form.Item>
                        <Form.Item label="S in thoi" name="phone" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <CInput name="phone" placeholder="S in thoi" />
                        </Form.Item>
                        <Form.Item label="a ch" name="address" rules={[{ required: true, message: 'Vui lng nhp thng tin' }]}>
                            <CInput name="address" placeholder="a ch" />
                        </Form.Item>
                        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                            <CButton CButton type="primary" htmlType="submit" loading={isLoading}>
                                To bnh n</CButton>
                        </Form.Item>
                    </Form>
            }
        </FCenter >
    );
};
